:root {
  --flex-item: 5;
  --flex-item-gap: 20px;
  --main-text-color: #111;
  --main-text-color-60: rgba(17, 17, 17, .6);
  --extra-text-color: #4f2ee8;
  --sub-text-color: #b4afaf;
  --light-text-color: #fff;
  --light-text-color-60: rgba(255, 255, 255, .6);
  --place-text-color: #eac645;
  --dark-text-color-60: rgba(0, 0, 0, .6);
  --main-bg-color: #111;
  --extra-bg-color: #4f2ee8;
  --place-bg-color: #eac645;
  --bg-color: #f6f6f6;
  --extra-border-color: #4f2ee8;
  --extra-border-color-40: rgba(79, 46, 232, .4);
  --empty: #fff;
  --transition-property: color, background, box-shadow;
  --transition-duration: .3s;
  --transition-timing-function: ease-out;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--main-text-color);
  letter-spacing: -.04em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.global-title, .global-p {
  margin: 0;
}

.global-link {
  color: currentColor;
  text-decoration: none;
}

.global-button {
  color: currentColor;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  letter-spacing: inherit;
  cursor: pointer;
  border: none;
}

.global-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.flex-container {
  gap: var(--flex-item-gap);
  flex-wrap: wrap;
  display: flex;
}

.main-block > .container {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .main-block > .container {
    gap: 100px;
  }
}

@media screen and (min-width: 1440px) {
  .main-block > .container {
    flex-direction: row;
    gap: 40px;
  }
}

.asides {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .asides {
    flex-direction: row;
    gap: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .asides {
    flex-direction: column;
    flex: auto;
    gap: 86px;
  }
}



.support-aside {
  width: 335px;
  height: 352px;
  background: radial-gradient( 183.6% 183.01% at 59.76% -23.42%, var(--extra-text-color) 18.03%, var(--light-text-color) 100% );
  border-radius: 10px;
  position: relative;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

.support-aside, .support-item a {
  color: var(--light-text-color);
}

.support-list {
  height: 200px;
  scroll-behavior: smooth;
  padding-left: 0;
  overflow-y: scroll;
}

.support-list::-webkit-scrollbar {
  display: block;
}

.support-img-01 {
  width: 131px;
}

.support-img-02 {
  width: 62px;
}

.support-img-03 {
  width: 101px;
}

.support-img-04 {
  width: 82px;
}

.support-img-05 {
  width: 55px;
}

.support-img-06 {
  width: 116px;
}

.support-img-07 {
  width: 149px;
}

.support-img-08 {
  width: 133px;
}

.support-img-09 {
  width: 81px;
}

.support-title {
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  padding-top: 24px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.14;
  display: flex;
}

.support-title img {
  margin-left: 12px;
}

.support-svg {
  height: 5.98px;
  width: 11.67px;
  fill: var(--extra-text-color);
  display: block;
}

.support-button {
  background: var(--light-text-color);
  border-radius: 50%;
  padding: 13.1px 10.17px 12.93px;
  transition: transform .3s;
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.support-button.support-button-rotate {
  transform: translate(-50%, -50%)scale(1)rotate(180deg);
}

.support-item {
  align-items: center;
  gap: 16px;
  margin-bottom: 15px;
  margin-left: 40px;
  display: flex;
}

.item-number {
  line-height: 1.29;
}

@media screen and (min-width: 376px) and (max-width: 767px) {
  .support-aside {
    height: 472px;
  }

  .support-button {
    bottom: 0;
  }
}

@media screen and (min-width: 768px) {
  .support-aside {
    width: 356px;
    height: 490px;
  }
}

@media screen and (min-width: 376px) {
  .support-list {
    height: 300px;
  }

  .support-svg {
    height: 6.58px;
    width: 12.83px;
  }

  .support-button {
    padding: 15.8px 12.58px 15.62px;
  }
}





